import moment from "moment";
import { Rupees, calender, comment, trash } from "../../Utils/Icons";

const IncomeItem = ({id,title,amount,category,description,date,deleteItem,type,totalIncome}) => {
    return ( 
        <div className="IncomeItem">
            
            <h5>{title}</h5>
            <div className="InnerContent">
                <p>{Rupees}{amount}</p>
                <p>{calender}{moment(date).format('DD/MM/YYYY')}</p>
                <p>{comment}{description}</p>
            </div>
            <div className="DelteBtn expense income">
                <button onClick={()=>deleteItem(id)}>{trash}</button>
            </div>
           
        </div>
     );
}
 
export default IncomeItem;