import { useState } from "react";
import { useGlobalContext } from "../../Context/globalContext";
import ReactDatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const ExpenseForm = () => {
  const [inputState, setInputState] = useState({
    title: "",
    amount: "",
    date: new Date(), // Set initial date here
    category: "",
    description: "",
  });
  const { addExpense } = useGlobalContext();
  const { title, amount, date,category, description } = inputState;
  const handleInput = (name) => (e) => {
    setInputState({ ...inputState, [name]: e.target.value });
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    const formattedDate = `${date.getDate()}/${
      date.getMonth() + 1
    }/${date.getFullYear()}`;
    addExpense({ ...inputState, date: formattedDate });
    setInputState({
      title: "",
      amount: "",
      category: "",
      date: new Date(), // Resetting date to initial value
      description: "",
    });
  };
 
  return (
    <div className="Form ">
      {/* //Expense */}
      <form action="/transaction/add-income" method="post" onSubmit={handleSubmit}>
        <div className="InputControl">
          <input
            type="text"
            value={title}
            name={"title"}
            placeholder="expense title"
            onChange={handleInput("title")}
          />
        </div>

        <div className="InputControl">
          <input
            type="text"
            value={amount}
            name={"amount"}
            placeholder="expense amount"
            onChange={handleInput("amount")}
          />
        </div>

        <div className="InputControl">
          <ReactDatePicker
            id="date"
            placeholderText="Enter A date"
            selected={date ? new Date(date) : null}
            dateFormat="dd/MM/yyyy"
            onChange={(date) => {
              setInputState({ ...inputState, date: date });
            }}
          />
        </div>
        <div className="Selects InputControl">
          <select
            required
            value={category}
            name="category"
            id="category"
            onChange={handleInput("category")}
          >
            <option value=" " disabled>
              Select Option
            </option>
            <option value="salary">Grocery</option>
            <option value="freelancing">Electricity Bill</option>
            <option value="pocket-money">Party</option>
            <option value="cashback">Stationary</option>
            <option value="cashback">Basic need</option>
            <option value="cashback">Medical</option>
            <option value="cashback">Dresses</option>
            <option value="cashback">College Fees</option>
          </select>
        </div>
        <div className="InputControl">
          <textarea
            name="description"
            value={description}
            placeholder="Add a reference "
            id="description"
            cols="30"
            rows="4"
            onChange={handleInput("description")}
          ></textarea>
        </div>

        <div className="Submit-Btn">
          <button type="submit">+ADD</button>
        </div>
      </form>
    </div>
  );
};

export default ExpenseForm;
