import { useGlobalContext } from "../../Context/globalContext";
import { comment } from "../../Utils/Icons";
import './transactionstyles.css' ; 
const Transaction = () => {
    const { transactionHistory } = useGlobalContext();
    const history = transactionHistory();

    return (
        <div className="Transaction">
            <h1>Transaction...</h1>
            <div className="Card">
                {history.map((item) => {
                    const { _id, title, amount, type, createdAt ,description} = item;
                    return (
                      <div
                        key={_id}
                        className="History_Item"
                        style={{
                          border:
                            type === "expense"
                              ? "3px solid #590e0e;"
                              : "3px solid rgb(21, 21, 139) ",
                        }}
                      >
                        <p className="element title">{title}</p>
                        <p className="element">
                          {type === "expense" ? `-${amount}` : `+${amount}`}
                        </p>
                        <p className="element">
                          {new Date(createdAt).toDateString()}
                        </p>
                        {/* Assuming description is passed as a prop */}
                        <p className="element">
                          {comment}
                          {description}
                        </p>
                      </div>
                    );
                })}
            </div>
        </div>
    );
}

export default Transaction;