import { useEffect } from 'react';
import { useGlobalContext } from '../../Context/globalContext';
import Form from '../Form/IncomeForm';
import './incomestyles.css'
import IncomeItem from '../IncomeItem/IncomeItems';
import { Rupees } from '../../Utils/Icons';
const Income = () => {
   
    const {getIncomes,incomes ,deleteIncome ,totalIncome} = useGlobalContext() ; 
    useEffect(()=>{
        getIncomes();
    },[]);
    return ( 
        <div className="Income">
            <div className="TotalIncome-income">
                <h1>Total Income:  {Rupees} {totalIncome()}</h1>
            </div>
            <div className="Main_Container">
            <div className="IncomeForm">
               <Form />
            </div>
            <div className="IncomeList">
                {incomes.map((income)=>{
                    const{_id,title,amount,date,category,description}=income ;
                    return <IncomeItem
                    key={_id} 
                    id={_id}
                    title={title}
                    description={description}
                    amount={amount}
                    date={date}
                    category={category}
                    deleteItem={deleteIncome}
                    totalIncome={totalIncome}
                    />
                })}
            </div>
            </div>
        </div>
     );
}
 
export default Income;

