import { useEffect } from 'react';
import { useGlobalContext } from '../../Context/globalContext';
import { Rupees } from '../../Utils/Icons';
import ChartTable from '../ChartTable/ChartTable';
import './dashboardstyles.css'

const Dashboard = () => {
    const{totalExpense , totalIncome ,getIncomes,getExpenses} = useGlobalContext() ;
    useEffect(()=>{
        getIncomes() 
        getExpenses()
    },[])
    return ( 
        <div className="DashBoard">
            <div className="Dash">
            <h1>DashBoard</h1>
               <div className="Chart"  >
                   <ChartTable/>
               </div>
         <div className="Total">
            <div className=" cont TotalIncome">
                 <p>Total Income:</p>
                 <p> {Rupees}{totalIncome()}</p>
            </div>
            <div className=" cont TotalExpense">
                <p>Total Expense:</p>
                <p>{Rupees}{totalExpense()}</p>
            </div>
            
        </div>
        </div>
           </div>
        
     );
}
 
export default Dashboard;