import { useState } from "react";
import { useGlobalContext } from "../../Context/globalContext";
import ReactDatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css" ;

const Form = () => {
    const [inputState , setInputState] =useState({
        title: '',
        amount: '',
        date: new Date(), // Set initial date here
        category: '',
        description: '',
    })
    const {addIncome}=useGlobalContext();
    const {title,amount,date,category,description} = inputState ; 
    const handleInput = name=>e=>{
        setInputState({...inputState ,[name]:e.target.value}) ; 
    }
    const handleSubmit = e=>{
        e.preventDefault();
        const formattedDate = `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
        addIncome({ ...inputState, date: formattedDate });
        setInputState({
            title: '',
            amount: '',
            date: new Date(), // Resetting date to initial value
            category: '',
            description: '',
        });
        
    }
    return ( 
        <div className="Form" >
            <form action="/transaction/add-income"  method="post"  onSubmit={handleSubmit}>
            <div className="InputControl">
                <input type="text"
                 value={title} 
                 name={'title'} 
                 placeholder="salary title"
                  onChange={handleInput('title')}/>
            </div>

            <div className="InputControl">
            <input type="text"
                 value={amount} 
                 name={'amount'} 
                 placeholder="salary amount"
                  onChange={handleInput('amount')}/>
            </div>
            
            <div className="InputControl">
                <ReactDatePicker id='date'
                placeholderText="Enter A date"
                selected={date ? new Date(date) : null}
                dateFormat="dd/MM/yyyy"
                onChange={(date) => {
                    setInputState({ ...inputState, date: date });
                }}
                />
                
            </div>
           
            <div className="Selects InputControl">
                <select required value={category} name="category" id="category" onChange={handleInput("category")} >
                    <option value=" "disabled>Select Option</option>
                    <option value="salary">salary</option>
                    <option value="freelancing">freelancing</option>
                    <option value="pocket-money">pocket-money</option>
                    <option value="cashback">cashback</option>
                </select>
             </div>

            <div className="InputControl">
                <textarea name="description" value={description} placeholder="Add a reference " id="description" cols ="30" rows="4" onChange={handleInput('description')}></textarea>
            </div>

            <div className="Submit-Btn">
               <button type="submit">+ADD</button>
            </div>
            </form>
        </div>
     );
}
 
export default Form;